<template>
  <div id="comment-list" class="border-box pL20 pR20">
    <div class="text-center nuList" v-if="list.length===0">
      暂无相关评论
    </div>
    <div class="list" v-if="list.length!==0">
      <div class="clear li" v-for="(item, index) in list" :key="item.index">
        <div class="left fl">
          <div class="img">
            <img :src="item.comment.user_profile_image_url" alt=""
            onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
          </div>
        </div>
        <div class="right fl">
          <div class="p1 clear">
            <div class="fl">{{item.comment.user_name}}</div>
          </div>
          <div class="p2">
            {{item.comment.text}}
          </div>
          <div class="p3 clear">
            <div class="fl">{{$utils.timeFormat(item.comment.create_time * 1000)}}</div>
          </div>
          <div class="digg" :class="{active: item.comment.is_followed!==0}">
            <div class="fr thumbs mR10" @click="thumbFn(item, index)">
              <img src="../../assets/img/index/digg.png" v-if="item.comment.is_followed===0" alt="">
              <img src="../../assets/img/index/digg_active.png" v-if="item.comment.is_followed!==0" alt="">
            </div>
            <div>{{$utils.thousandRead(item.comment.digg_count) === 0 ? '赞' : $utils.thousandRead(item.comment.digg_count)}}</div>
          </div>
        </div>
      </div>
      <div class="no-more-data" v-if='total - list.length < 10'>{{noMessageTip}}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'comment-list',
  props: {
    list: {       // 评论列表
      type: Array,
      default: null
    },
    replyBol: {       // 是否有回复
      type: Boolean,
      default: true
    },
    total: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {

    }
  },
  computed: {
    noMessageTip: function () {
      return this.$store.state.common.noMessageTip
    },
    commonState () {
      return this.$store.state.common
    }
  },
  methods: {
    // 点赞
    thumbFn (item, index) {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (item.comment.is_followed === 1) {
        return
      }
      item.comment.is_followed = 1
      item.comment.digg_count++
    }
  }
}
</script>

<style lang="scss" type="text/css">
#comment-list{
  width: 100%;
  margin-bottom: 100px;
  padding: 0 30px;
  img{
    width: 100%;
    height: 100%;
  }
  .li{
    margin-top: 55px;
    position: relative;
  }
  .right{
    padding-left: 10px;
    width: 615px;
    color: #a2a2a2;
    .thumbs{
      width: 24px;
      height: 24px;
    }
    .p1{
      font-size: 24px;
      line-height: 34px;
      .fl {
        color: rgba(102,120,140,0.86);
      }
    }
    .p2{
      font-size: 32px;
      word-wrap: break-word;
      color: #333;
      line-height: 46px;
      margin: 2px auto 4px;
    }
    .p3{
      font-size: 24px;
      line-height: 34px;
      color: #B3C5D8;
    }
    .Reply{
      background: #F4F5F7;
      padding: 0 18px;
      text-align: center;
      font-size: 26px;
      border-radius: 15px;
    }
    .digg {
      position: absolute;
      font-size: 24px;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      color: #7086A0;
      top: 0;
      right: 0;
      .thumbs {
        width: 30px;
        height: 30px;
      }
      &.active{
        color: rgb(255, 96, 97)
      }
    }
  }
  .left{
    width: 62px;
    .img{
      width: 51px;
      height: 51px;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .nuList{
    color: #a2a2a2;
    line-height: 150px;
    font-size: 24px;
  }
}
</style>



