<template>
  <div id="ArticleDetails" v-if="!Array.isArray(this.details)">
    <div class="topTxt" v-if="$config.source_style === 6 || $config.source_style === 7">
      <div class="imgBox" @click="backToIndex">
        <img src="../../assets/img/black-left.png" alt="">
      </div>
      文章详情
    </div>
    <div v-if="$config.source_style === 6 || $config.source_style === 7" class="pT90"></div>
    <!-- 屏蔽了左右滑动事件 -->
    <!-- <v-touch class="abc" :class="{'pT90':($config.source_style === 6 || $config.source_style === 7)}" @swipeleft="onSwipeLeft" tag="div"> -->
      <div class="hearder">
        <div class="font-600 f40 articleTitle lh56 color3A">{{details.title}}</div>
        <div class="attention clear">
          <div class="fl p1">
            <img :src="details.media_user?details.media_user.avatar_url:''"
            alt=""
            onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
          </div>
          <div class="fl p2">
            <div class="name f28">{{details.media_user.screen_name}}</div>
            <div class="time clear f20 mT10">
              <div class="fl mR12">{{$utils.timeFormat(details.publish_time * 1000)}}</div>
              <div class="fl"> 阅读  {{$utils.thousandRead($utils.cache('read_count'))}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 文章内容 -->
      <div ref="detailsContent" :class="{'fullText':expandBol}">
        <div class="details-content" v-html="details.content"></div>
        <div class="source clear f30 mT30">
          <div class="fl">来源：{{details.source}}</div>
        </div>
      </div>
      <!-- 展开全文 1000px -->
      <div class="expand clear"  @click="expandBtn" v-if="expandBol">
        <div class="fl txt">展开全文</div>
        <div class="fl img">
          <img src="../../assets/img/under.png" alt="">
        </div>
        <div class="fl box"></div>
      </div>
      <!-- <div class="clear recommend pB10">
        <div class="fl pB20 f36 color50">
          精彩推荐
        </div>
      </div> -->
      <news-list
      :list="list"
      @upDate="upDate"
      @isgoblack="isgoblack=false"
      class="wonderful"></news-list>
      <hot-list
      :details="details"
      @upperLevel="upperLevel"
      :page="page"
      @total="getTotal"></hot-list>
    <articleMd v-if="isShow"></articleMd>
    <loop-list class="bottomTip" v-if="articleList.length > 0 && $route.query.inviteCode" @showQQ='change' :list='articleList'></loop-list>
  </div>
</template>

<script>
import NewsList from '../common/news-list'
import hotList from './hot-list'
import HeaderNav from '../common/header-nav'
import articleMd from '../common/article-md'
import { mapState } from 'vuex'
export default {
  name: 'ArticleDetails',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      count: 0,
      msg: '文章详情',
      list: [],
      details: [],
      isgoblack: true,     // 是否需要监听aid
      toScrollBol: false,  // 是否需要滚动到指定位置
      page: 1,             // 评论第几页
      total: 0,            // 评论总条数
      detailsArr: [],      // 存储跳转过的文章
      pageNum: 0,          // 可以分多少页
      expandBol: false,    // 是否有展开全文按钮
      oldHeight: true,     // 刷新文章是否保持滚动条回顶部
      timestamp: null,      // 时间戳
      isShow: false,         // 首次阅读显示
      articleList: []       // 奖励轮播数据
    }
  },
  methods: {
    // 返回上一级
    upperLevel () {
      this.$router.go(-1)
    },
    // 调取获得文章奖励的接口
    getReward () {
      if (this.$utils.isGetReard(this.$route.params.aid)) {
        this.$api.post('ARCREWARD_TIP', {aid: this.$route.params.aid, type: 'arc'}, res => {
          if (res.data.coin > 0) {
            if (!this.$utils.cache('first_tips')) {
              this.$utils.cache('first_tips', 1)
              this.isShow = true
            } else if (this.$config.source_style >= 6) {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App读文章额外多赚了${res.data.extra_coin}金币`}, res.data)
              if (!this.$utils.cache('isFirstRead')) {
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.$utils.cache('isFirstRead', 1)
              } else {
                this.set_shareSuccessObj({
                  type: 7,
                  shareSuccessObj
                })
                this.set_shareType(7)
              }
              this.setShowShare(true)
            } else {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App读文章赚更多金币`}, res.data)
              this.set_shareSuccessObj({
                type: 7,
                shareSuccessObj
              })
              this.set_shareType(7)
              this.setShowShare(true)
            }
            // 已获得奖励设置无奖励
            this.$utils.setArticle(this.$route.params.aid, 'article')
          }
        })
      }
    },
    getContent () {
      if (this.articleList.length === 0 && this.$route.query.inviteCode) {
        this.$api.get('ARTICLE_LOOP_DATA', {invitation_code: this.$route.query.invitation}, res => {
          this.articleList = res.data
        })
      }
    },
    // 点击展开全文
    expandBtn () {
      let h = this.$utils.getScrollTop()   // 记录滚动条高度
      this.expandBol = false
      this.$nextTick(() => {
        window.scrollTo(0, h)
      })
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.timer = setInterval(() => {
        this.count++
        if (this.count >= 15) {
          this.getReward()
          clearInterval(this.timer)
          this.timer = null
        }
      }, 1000)
    },
    // 获取上拉列表
    upList () {
      if (this.pageNum > this.page) {       // 有剩余分页的时候才可以触发
        if (this.$utils.scrollHeight() === 0) {
          this.page++
        }
      }
    },
    // 获取详情
    getDetails () {
      let data = {id: 'i' + this.$route.params.aid}
      this.$api.getNoCode('LOCAL_INFO', data, (res) => {
        // this.details = detailsJson.data
        // document.title = detailsJson.data.title
        this.details = res.data
        // 没有详情时跳转404页面  (没有数据时反悔了一个空数组)
        if (Array.isArray(this.details)) {
          this.$router.replace('/notFound')
          return
        }
        // 邀请码不存在的时候阅读文章主动触发奖励
        if (!this.$route.query.inviteCode) {
          this.expandBtn()
        }
        document.title = res.data.title
        if (this.oldHeight) {    // 刷新文章是否保持滚动条回顶部
          window.scrollTo(0, 0)
        }
        this.oldHeight = true
        this.$nextTick(() => {
          if (this.$refs.detailsContent.offsetHeight > 1000) {
            if (this.$route.query.inviteCode) {
              this.expandBol = true
            } else {
              this.expandBtn()
            }
            this.isgoblack = true         // 是否需要监听aid
            if (this.toScrollBol) {
              window.scrollTo(0, this.detailsArr[0].scroll)
              this.toScrollBol = false
              this.detailsArr.splice(0, 1)
            }
          }
        })
      })
    },
    // 获取精彩推荐列表
    getRecommend () {
      this.$api.get('AID_RECOMMEND', {aid: this.$route.params.aid}, (res) => {
        this.list = res.data || []
      })
    },
    // 是否关注
    isFollow () {
      let data = {}
      data.mid = this.details.mid * 1
      data.status = this.details.is_follow === 0 ? 1 : 2
      this.$api.post('IS_FOLLOW', data, (res) => {
        this.details.is_follow = this.details.is_follow === 0 ? 1 : 0
        this.$utils.refreshAttention(data.status, data.mid)
      })
    },
    // 获取总评论条数
    getTotal (total) {
      this.total = total
      this.pageNum = Math.ceil(this.total / 10)
    },
    // 离开文章回调
    leave (aid) {
      if (this.$utils.cache('user_token')) {
        let data = {aid, starttime: this.timestamp, type: 'arc'}
        this.$api.get('LEAVE', data, (res) => {
        })
      }
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    upDate () {
      let data = {}
      data.aid = this.$route.params.aid
      data.scroll = this.$utils.getScrollTop()
      this.detailsArr = [data, ...this.detailsArr]
      this.initData()
    },
    // 初始化阅读数据
    initReward () {
      clearInterval(this.timer)
      this.timer = null
      this.count = 0
    },
    // 初始化
    initData () {
      this.getDetails()
      this.getRecommend()
      this.initReward()
    },
    // 初始化回到首页
    backToIndex () {
      this.$router.go(-1)
      // 对微信进行处理如果400ms之后还在详情，主动跳转至首页（此时无上级路由）
      setTimeout(() => {
        if (this.$route.name === 'LocalDetail') {
          this.$route.replace('/')
        }
      }, 400)
    },
    // 首次阅读方法
    toggleIs () {
      this.$api.post('READ_60MINUTE', {aid: this.$route.params.aid, type: 'arc'}, res => {
        if (res.data.first_tips) {
          this.$utils.cache('first_tips', res.data.first_tips)
          this.isShow = true
        }
      })
    }
  },
  mounted () {
    this.timestamp = (new Date().getTime()) / 1000
    this.aid = this.$route.params.aid
    this.$utils.cache('rewardId', this.aid)
    this.initData()
    window.onscroll = this.upList
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.oldHeight = false
        let h = this.$utils.getScrollTop()   // 记录滚动条高度
        this.initData()
        this.$nextTick(() => {
          window.scrollTo(0, h)
        })
      }
    },
    '$route.params.aid' (a, b) {
      this.$utils.cache('rewardId', a)
      if (this.commonState.login_bol) {
        this.leave(b)
        // aid改变将上一个设置为离开，时间戳重新开始计算
        this.timestamp = this.$utils.getTimeStamp()
      }
      if (this.isgoblack) {
        this.toScrollBol = true
        this.initData()
      }
    }
  },
  destroyed () {
    this.leave(this.$utils.cache('rewardId'))
  },
  components: {
    NewsList,
    hotList,
    HeaderNav,
    articleMd
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
#ArticleDetails{
  .topTxt{
    line-height: 90px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #eee;
    text-align: center;
    position: fixed;
    width: 100%;
    z-index: 1;
    .imgBox{
      position: absolute;
      width: 18px;
      height: 32px;
      padding: 30px 29px;
      line-height: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .fullText{
    height: 2000px;
    overflow: hidden;
  }
  .expand{
    text-align: center;
    margin: 20px;
    font-size: 28px;
    line-height: 36px;
    color: #5784df;
    position: relative;
    .box{
      position: absolute;
      width: 100%;
      height: 30px;
      background: linear-gradient(to top, #fff, rgba(255,255,255,0.3));;
      top: -50px;
    }
    .txt{
      margin-left: 290px;
    }
    .img{
      width: 18px;
      height: 12px;
      margin-left: 10px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .abc{
    touch-action: pan-y !important;
  }
  .recommend{
    padding-left: 34px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .content{
    width: 100%;
    padding-left: 34px;
    padding-right: 22px;
    box-sizing: border-box;
  }
  .source{
    padding-left: 34px;
    color: #a3a3a3;
    line-height: 56px;
  }
  .hearder{
    padding-left: 34px;
    padding-right: 34px;
    padding-top: 32px;
    padding-bottom: 40px;
    user-select: text;
    .attention{
      margin-top: 24px;
      .followBtn,.followBtns{
        font-size: 26px;
        width: 130px;
        height: 46px;
        line-height: 46px;
        color: #fff;
        border-radius: 8px;
        text-align: center;
        background: #bdbcbc;
      }
      .followBtns{
        background: #e54256;
      }
      .hook{
        background: #BDBCBC;
      }
      .p3{
        width: 130px;
        height: 46px;
        border-radius: 8px;
        margin-top: 10px;
        vertical-align: middle;
        color: #fff;
        .imgs{
          width: 26px;
          height: 26px;
          margin-top: 6px;
          margin-left: 10px;
          margin-right: 10px;
        }
        img{
          width: 100%;
          height: 100%;
        }
      }
      .p2{
        .time{
          color: #999;
        }
      }
      .p1{
        width: 62px;
        height: 62px;
        margin-right: 8px;
        img{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
    }
  }
}
</style>



