<template>
  <div id="commentList" ref="comment" v-if="$config.source_style >= 6">
    <!-- <div class="clear recommend pB10">
      <div class="fl pB20 f36 color50">
        热门评论
      </div>
    </div> -->
    <comment-list :list="list" :total='total'></comment-list>
    <div class="footer clear" :class="{'positioning':!inputBol}">
      <div class="fl oDiv1" @click="toIndex">
        <img src="../../assets/img/detail_back.png" alt="">
      </div>
      <label class="fl oDiv2" @click="setComment">
        <div class="editIcon iconSize">
          <img src="../../assets/img/editIconGray.png" alt="编辑图标">
        </div>
        <div class="txt">写评论...</div>
      </label>
      <div class="fl oDiv3" @click="commentTarget">
        <img src="../../assets/img/detail_message.png" alt="">
        <div class="num" v-if="total!==0">{{total > 99 ? '99+' : total}}</div>
      </div>
      <div class="fr mR30 oDiv5 clipbtn" @click="change"  :data-clipboard-text="cppyLink">
        <img src="../../assets/img/detail_share.png" alt="">
      </div>
    </div>
    <div class="comment clear" :class="{'positioning':inputBol}" >
      <textarea
        id="comment"
        placeholder="优质评论将被优先展示..."
        class="fl"
        v-model="content"
        maxlength="100"
        ref="comments"
        @focus="inputFocus"
        @blur="inputBlur">
      </textarea>
      <label for="comment">
        <div class="fr oDiv pL30" v-if="content.length===0" @click="release">
          发布
        </div>
      </label>
      <div class="fr oDiv pL30 btnColor" v-if="content.length!==0" @click="release">
        发布
      </div>
      <!-- 评论遮罩层 -->
      <div class="mask"></div>
    </div>
    <share-it v-if="shareBol" @closeShare='closeShare' ref="share" :details="details"></share-it>
  </div>
</template>

<script>
import commentList from './comment-list'
import shareIt from '../home/share-it'

// import config from './../../assets/js/config'
import Clipboard from 'clipboard'
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'

import { mapState, mapMutations } from 'vuex'
export default {
  name: 'hot-comment',
  props: {
    details: {
      type: Object,
      default: null
    },
    page: {                // 第几页
      type: Number,
      default: 1
    }
  },
  computed: mapState({
    commonState: state => state.common,
    login_bol: state => state.login_bol
  }),
  data () {
    this.cppyLink = location.origin + '?path=' + this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
    return {
      setFocus: false,
      list: [],            // 评论列表
      per_page: 10,        // 一页多少条
      shareBol: false,     // 分享bol值
      inputBol: false,     // 是否评论
      content: '',      // 评论内容
      total: 0,            // 总评论数
      pageBol: false,       // false位合并评论列表
      setdomain: '' // 配置中转页域名
    }
  },
  methods: {
    // 评论定位
    commentTarget () {
      if (this.$refs.comment.offsetHeight < this.$utils.getClientHeight()) {
        window.scrollTo(0, this.$utils.getScrollHeight())
      } else {
        window.scrollTo(0, this.$refs.comment.offsetTop)
      }
    },
    ...mapMutations([
      'common_login_box'
    ]),
    // 发布评论
    release () {
      if (!this.content.trim()) {
        // 空的话不做任何反应
        this.$toast({
          message: '请填写评论后发布',
          position: 'center',
          duration: 1500
        })
      } else if (!this.commonState.login_bol) {
        // 没有登录的话就要登录后才可以发布评论
        this.common_login_box(true)
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'bottom',
          duration: 1500
        })
      } else {
        let data = {}
        data.comment = {}
        data.comment.text = this.content.trim()
        data.comment.digg_count = 0
        data.comment.user_name = this.$utils.cache('nickname')
        data.comment.create_time = Math.floor(new Date().getTime() / 1000)
        data.comment.is_followed = 0
        data.comment.user_profile_image_url = this.commonState.userInfo.avatar
        this.list.splice(0, 0, data)
        if (this.page === 1) {
          // this.getComments()
        } else if (this.page !== 1) {
          this.pageBol = true
          this.$emit('pageOne')
        }
        // 获取完数据以后进行滚动条定位
        setTimeout(() => {
          this.commentTarget()
          this.total = this.total + 1
        }, 50)
        this.content = ''
      }
    },
    inputFocus () {
      this.inputBol = true
      let u = navigator.userAgent
      let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)  // ios终端 
      if (isiOS) {
        window.setTimeout(() => {
          window.scrollTo(0, document.body.clientHeight)
        }, 100)
      }
    },
    inputBlur () {
      this.inputBol = false
    },
    // 加载评论列表 ,传过去的aid:this.$route.params.mid
    getComments () {
      let data = {group_id: this.$route.params.aid, page: this.page, per_page: this.per_page}
      this.$api.getNoCode('LOCAL_COMMENT', data, (res) => {
        this.list = res.data || [] // 填充数组
        this.total = res.total_number     // 总评论数
      })
    },
    // 回到首页
    toIndex () {
      // this.$router.push({path: '/'})
      this.$emit('upperLevel')
    },
    // 关闭分享组件
    closeShare () {
      this.shareBol = false
    },
    // 加载微信签名
    loadWxSignature () {
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          this.$api.get('USE_DOMAIN', { type: 'temp' }, (res) => {
            let newdomain = res.data.domain
            let sig = res.data
            this.pageUrl = sig.url
            let contentEl = document.createElement('div')
            
            contentEl.innerHTML = this.details.content
            let content = contentEl.innerText
            var shareContent = {
              title: this.details.title,
              desc: content, // 分享描述
              link: newdomain + `?inviteCode=${this.$utils.cache('inviteCode')}&path=${this.$route.path}`, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: this.details.img_url && this.details.img_url.length > 0 ? this.details.img_url[0] : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
            }
            // 配置微信签名信息
            wx.config({
              debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
              appId: sig.appid, // 必填，公众号的唯一标识
              timestamp: sig.timestamp, // 必填，生成签名的时间戳
              nonceStr: sig.nonceStr, // 必填，生成签名的随机串
              signature: sig.signature, // 必填，签名
              jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
            })
            wx.ready(function () {
              // 分享到QQ空间
              wx.onMenuShareTimeline({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                }
              })
              wx.onMenuShareAppMessage({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                }
              })
              wx.onMenuShareQQ({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                }
              })
            })
            wx.error((res) => {
            })
          })
        }
      })
    },
    setComment () {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      this.$refs.comments.focus()
      setTimeout(() => {
        this.setFocus = true
      }, 200)
    },
    change () {
      if (!this.commonState.login_bol) {
        if (this.$utils.isWx()) {
          this.$utils.wxLogin()
        } else {
          this.$store.commit('common_login_box', true)
        }
        return
      }
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        this.shareBol = true
      } else {
        if (this.$utils.isWx()) {
          this.shareBol = true
        } else {
          if (Clipboard.isSupported()) {
            let clb = new Clipboard('.clipbtn')
            clb.on('success',  (e) => {
              this.$toast({
                message: '已复制文章链接,快发送给好友吧',
                position: 'middle',
                duration: 1500,
                iconClass: 'icon icon-success'
              })
              clb.destroy()
            })
            clb.on('error',  (e) => {
              this.$toast({
                message: '复制链接失败了',
                position: 'middle',
                duration: 1500,
                iconClass: 'icon icon-success'
              })
              clb.destroy()
            })
          }
        }
      }
    },
    // 初始化
    initData () {
      this.page = 1
      this.getComments()
    }
  },
  mounted () {
    this.initData()
    this.loadWxSignature()
    this.inviteCode = this.$store.state.inviteCode
  },
  watch: {
    'details' () {
      this.initData()
    },
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
      }
    },
    total () {
      this.$emit('total', this.total)
    },
    page () {
      if (this.pageBol) {  // 是否需要合并评论列表 true位不合并
        this.getComments()
        this.pageBol = false
      } else {
        let data = {group_id: this.$route.params.aid, page: this.page, per_page: this.per_page}
        this.$api.getNoCode('LOCAL_COMMENT', data, (res) => {
          const lists = res.data || []
          this.list = [...this.list, ...lists]
          this.total = res.total_number     // 总评论数
        })
      }
    }
  },
  components: {
    shareIt,
    commentList
  }
}
</script>

<style lang="scss" scoped>
#commentList{
  width: 100%;
  .recommend{
    padding-left: 21px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .lala{
    width: 750px;
    height: 100%;
    background: #fff;
  }
  img{
    width: 100%;
    height: 100%;
  }
  .comment{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: 5000px;
    height: 250px;
    background: #fff;
    width: 100%;
    .mask{
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.3);
      position: absolute;
      bottom: 250px;
    }
    .oDiv{
      margin-right: 30px;
      margin-top: 10px;
      background: #959596;
      color: #fff;
      border-radius: 8px;
      padding: 10px 20px;
      text-align: center;
    }
    .btnColor{
      background: #E54256;
    }
    textarea:focus {
      outline: none;
      resize: none;
    }
    textarea {
      width: 690px;
      height: 160px;
      margin-top: 20px;
      margin-left: 28px;
      border-radius: 20px;
      box-sizing: border-box;
      border: none;
      font-size: 30px;
      text-indent: 10px;
      padding: 25px;
      background: #F0F2F1;
    }
    textarea::-webkit-input-placeholder {
      color: #afafaf;
      font-size: 30px;
    }
  }
  .footer{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: -1000px;
    height: 100px;
    background: #fff;
    display: flex;
    align-items: center;
    width: 100%;
    line-height: 100px;
    .oDiv5{
      width: 46px;
      height: 49px;
      display: flex;
      align-items: center;
      margin: 0 30px;
    }
    .oDiv3,.oDiv4{
      position: relative;
      width: 49px;
      height: 49px;
      margin-left: 30px;
      display: flex;
      align-items: center;
      .num{
        position: absolute;
        top: -10px;
        left: 60%;
        font-size: 16px;
        padding: 5px;
        background: red;
        line-height: 18px;
        padding-left: 8px;
        padding-right: 8px;
        color: #fff;
        border-radius: 13px;
      }
    }
    .oDiv2{
      flex: 1;
      border-radius: 64px;
      height: 64px;
      position: relative;
      background: #f5f5f5;
      display: flex;
      flex-flow: wrap nowrap;
      align-items: center;
      .editIcon {
        width: 38px;
        height: 35px;
        display: flex;
        align-items: center;
        margin-left: 34px;
        margin-right: 17px;
      }
      .txt{
        color: #666;
        font-size: 24px;
      }
    }
    .oDiv1{
      width: 18px;
      height: 35px;
      padding: 10px 34px 10px 28px;
      line-height: 0;
    }
  }
  .positioning{
    bottom: 0;
  }
}
</style>



